.navbar {
    margin-top: 0;
    margin-right: 0;
    margin-left: 0;
    width: 100%;
    font-size: 16px;
    color: #fff;
    background-color: rgb(81,51,164);
    height: 100px;
    font-family: 'Aclonica', sans-serif;
    position: fixed;
  }
  
  .nav li {
    display: inline-block;
    padding: 26px 0;
    width: 25%;
  }
  
  ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
  }
  


